.footer-dark {
    padding:50px 0;
    color: #f0f9ff;
    background-color:#282d32;
    text-align: center;
  }
  
  .footer-dark h3 {
    margin-top:0;
    margin-bottom:12px;
    font-weight:bold;
    font-size:16px;
  }
  
  .footer-dark h3:hover {
    margin-top:0;
    margin-bottom:12px;
    font-weight:bold;
    color: #fff;
    font-size:16px;
  }
  .footer-dark ul {
    padding:0;
    list-style:none;
    line-height:1.6;
    font-size:14px;
    margin-bottom:0;
  }
  
  .footer-dark ul a {
    color:inherit;
    text-decoration:none;
    opacity: 0.6;
  }
  
  .footer-dark ul a:hover {
    opacity:0.8;
    color: #fff;
  }
  .fab{
    padding: 15px;
    transition: .5s;
  }
  
  .fab:hover {
    transition: .5s;
  }
  
  @media (max-width:767px) {
    .footer-dark .item:not(.social) {
      text-align:center;
      padding-bottom:20px;
    }
  }
  
  .footer-dark .item.text {
    margin-bottom:36px;
  }
  
  @media (max-width:767px) {
    .footer-dark .item.text {
      margin-bottom:0;
    }
  }
  
  .footer-dark .item.text p {
    opacity:0.6;
    margin-bottom:0;
  }
  
  .footer-dark .item.social {
    text-align:center;
    
  }
  
  @media (max-width:991px) {
    .footer-dark .item.social {
      text-align:center;
      margin-top:20px;
    }
  }
  
  .footer-dark .item.social > a {
    font-size:36px;
    width: 70px;
    height: 70px;
    line-height: 70px;
    display:inline-block;
    text-align:center;
    overflow: hidden;
    border-radius:50%;
    box-shadow:0 0 0 1px rgba(255,255,255,0.4);
    margin: 40px 20px;
    color:#fff;
    opacity:0.75;
    transition: .5s;
    transform: scale(.9);
  }
  
  
  .footer-dark .item.social > a:hover {
  box-shadow: 0 0 15px #fff;
  color: #fff;
  }
  
  .footer-dark .item.social {
    color: 0 0 15px #fff;
    }
  
  ul li {
    list-style: none;
    margin: 0 5px;
  }
  
  
  .footer-dark .copyright {
    text-align:center;
    padding-top: 20px;
    opacity:0.3;
    font-size:13px;
    margin-bottom:0;
  }
  
  
  @media (max-width: 500px) {
  .footer-dark .item.social > a {
    font-size: 18px;
    width: 26px;
    height: 26px;
    line-height: 26px;
    display:inline-block;
    text-align:center;
    overflow: hidden;
    border-radius:50%;
    box-shadow:0 0 0 1px rgba(255,255,255,0.4);
    margin:12px;
    color:#fff;
    opacity:0.75;
    transition: .5s;
    transform: scale(.9);
  }
  .fab{
    padding: 5px;
    transition: .5s;
  }
  }